#join-dialog h1 {
  color: #4d4d4d;
  font-weight: bold;
  text-align: center;
}

#img-div {
  text-align: center;
  position: absolute;
  top: 19%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#img-div img {
  height: 15%;
  width: 42.5%;
}

#join-dialog label {
  color: #0088aa;
}

#join-dialog input.btn {
  margin-top: 15px;
}

/* #session-header {
  border: 3px solid red;
  height: 150px;
} */

#session-title {
  display: inline-block;
}


#session-tools {
  float: right;
}

#video-container video {
  position: relative;
  float: left;
  cursor: initial;
}

#video-container p {
  display: inline-block;
  background: #f8f8f8;
  padding-right: 5px;
  padding-left: 5px;
  color: #777777;
  font-weight: bold;
  border-bottom-right-radius: 4px;
  cursor: default;

}

