#main-video p {
  position: absolute;
  display: inline-block;
  background: #f8f8f8;
  padding-right: 5px;
  padding-left: 5px;
  left: 0;
  font-size: 22px;
  color: #777777;
  font-weight: bold;
  border-bottom-right-radius: 4px;
}

#main-video video {
  cursor: initial;
}

/* #session img {
  width: 15%;
