  display: inline-block;
  object-fit: contain;
  vertical-align: baseline;
} */

#session #video-container img {
  position: relative;
  float: left;
  width: 50%;
  cursor: pointer;
  object-fit: cover;
  height: 180px;
}


/* xs ans md screen resolutions*/

@media screen and (max-width: 991px) {
  .vertical-center {
    width: 85%;
    padding-top: 10px;
    top: 57%;
  }
}


